@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Open Sans', sans-serif;
}

body {
  margin: 0;
  color:#ebebeb;
}

.slide-in-left-enter {
  transform: translateX(-100%);
}

.slide-in-left-enter-active {
  transform: translateX(0);
  opacity: 1;
  transition: all 200ms;
}

.slide-in-left-exit {
  transform: translateX(0%);
  opacity: 1;
}

.slide-in-left-exit-active {
  transform: translateX(-100%);
  opacity: 0;
  transition: all 200ms;
}

.center {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
.users-list {
    list-style: none;
    margin: 0 auto;
    padding: 0;
    width: 90%;
    max-width: 50rem;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
.user-item {
    margin: 1rem;
    width: calc(45% - 2rem);
    min-width: 17.5rem;
    display: flex;
  }
  
  .user-item a {
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
    text-decoration: none;
    padding: 1rem;
    color: white;
    background: #292929;
  }
  
  .user-item a:hover,
  .user-item a:active {
    background: #ffd900;
  }
  
  .user-item__content {
    text-align: center;
    padding: 0;
  }
  
  .user-item__image {
    width: 4rem;
    height: 4rem;
    margin-right: 1rem;
  }
  
  .user-item__info h1 {
    font-weight: 400;
    line-height: 1.2;
    margin-top: 0;
    padding-top: 12px;
    font-size: 1.75rem;
    margin: 0 0 0.5rem 0;
    margin-bottom: 18px;
    font-weight: normal;
    font-family: Ne10-simplified,"Helvetica Neue",Helvetica,Roboto,Arial,sans-serif;
    text-align: right;
    word-break: break-word;
    -webkit-hyphens: auto;
        -ms-hyphens: auto;
            hyphens: auto;
    font-weight: 400;
    line-height: 1.2;
    margin-top: 0;
    color: #053041;
  }
  
  .user-item:hover h2,
  .user-item:active h2,
  .user-item:hover h3,
  .user-item:active h3 {
    color: #292929;
  }
  
  .user-item__info h3 {
    margin: 0;
  }
.main-header {
    width: 100%;
    height: 6rem;
    display: flex;
    flex-direction: row;
    align-items: center ;
    position: fixed;
    top: 0;
    left: 0;
    background: #485d65;
    border:skyblue;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.26);
    padding: 0 3rem;
    z-index: 5;
    font-size: 16px;
    line-height: 1.5;
    color: #485d65;
    font-weight: 400;
    font-style: italic;
  }
  
  main {
    margin-top: 6rem;
  }
  
  @media (min-width: 768px) {
    .main-header {
      justify-content: space-between;
      box-sizing: border-box;
      font-family: Ne10-simplified,"Helvetica Neue",Helvetica,Roboto,Arial,sans-serif;
    }
  }
.nav-links {
    list-style: none;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: auto;
    align-items: left;
    font-size: 16px;
    line-height: 1.5;
    color: #485d65;
    font-weight: 400;
    list-style: none;
   position: relative;
  }
  
  .nav-links li {
    margin: 10rem;
    border: solid;

  }
  
  .nav-links a {
    border: 1px solid transparent;
    color: #292929;
    text-decoration: none;
    padding: 0.5rem;
  }
  
  .nav-links a:hover,
  .nav-links a:active,
  .nav-links a.active {
    background: #79a3af;
    border-color: #485d65;
    font-size: 16px;
    line-height: 1.5;
    color: #485d65;
    font-weight: 400;

  }
  
  .nav-links button {
    cursor: pointer;
    border: 1px solid #292929;
    color: #292929;
    background: transparent;
    padding: 0.5rem;
    font: inherit;
  }
  
  .nav-links button:focus {
    outline: none;
  }
  
  .nav-links button:hover,
  .nav-links button:active {
    background: #292929;
    color: white;
  }
  
  @media (min-width: 768px) {
    .nav-links {
      flex-direction: row;
    }
  
    .nav-links li {
      margin: 0 0.5rem;
    }
  
    .nav-links a {
      color: white;
      text-decoration: none;
    }
  
    .nav-links button {
      border: 1px solid white;
      color: white;
      background: transparent;
    }
    
    .nav-links button:hover,
    .nav-links button:active {
      background: #485d65;
      color: #292929;
    }
  }
  
.main-navigation__menu-btn {
    width: 5rem;
    height: 5rem;
    background: transparent;
    border: skyblue;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin-right: 2rem;
    cursor: pointer;
    align-items: center;
  }
  
  .navitems{
display: flex;
align-items: center;
justify-content: space-between;
font-size: 16px;
line-height: 1.5;
color: #485d65;
font-weight: 400;
}
  .spacer{
      flex: 1 1;
  }
 .header__logo a {
      background:#485d65;
      font-size: 2rem;
      text-decoration: none;
    }
  .main-navigation__menu-btn span {
    display: block;
    width: 3rem;
    height: 2.5px;
    background: white;
  }
  
  .main-navigation__title {
    color: white;
  }
  
  .main-navigation__title a {
    font-family: Ne10-simplified,"Helvetica Neue",Helvetica,Roboto,Arial,sans-serif;
    box-sizing: border-box;
    text-decoration: none;
    color: white;
  }
  
  .main-navigation__header-nav {
    display: none;
  }
  
  .main-navigation__drawer-nav {
    height: 100%;
  }
  
  @media (min-width: 768px) {
    .main-navigation__menu-btn {
      display: none;
    }
  
    .main-navigation__header-nav {
      display: block;
    }
  }
